import React from 'react'
import { Col, Container, Row, Table } from 'react-bootstrap'
import './Calculator.css';

const Calculator = () => {
    function pick(val) {
        document.getElementById('screen').value +=val;
    }
    function clr() {
        document.getElementById('screen').value= " ";
    }
    function remove() {
         let x=document.getElementById('screen').value;
        let y=x.slice(0,-1);
        document.getElementById('screen').value=y;

        
    }

    function arthmatic_opration() {
       let store=document.getElementById('screen').value;
        let store1=eval(store);
        document.getElementById('screen').value=store1;
        //let x=document.getElementById('screen').value=x.value(eval)
        if (store1 == undefined)
            {
                document.getElementById("screen").value="";
            }
        
    }
    function press_equal()
    {
        //document.getElementById('screen').value;
        if(document.getElementById('screen').value==0) {
            document.getElementById('screen').value="=";
        }
        else{
            // return value="=";
        }
       
    }
  return (
   <Container className='calculator_container'>
    <Row className='cal_table' >
        <h1 className='row_heading'>Calculator</h1>
        
        <Col>
        <Table>
        <tr>
            <th colSpan="4">
                <input className='txt' type="text" id="screen"/></th>
        

        </tr>
        <tr><th><input className='btn' type="button" value="1" onClick={() => pick(1)}/></th>
            <th><input className='btn' type="button" value="2" onClick={() => pick(2)}/></th>
            <th><input className='btn' type="button" value="3" onClick={() => pick(3)}/></th>
            <th><input className='btn' type="button" value="/" onClick={() => pick('/')}  style={{backgroundColor:"green"}}/></th>
        </tr>
        <tr><th><input className='btn' type="button" value="4"onClick={() => pick(4)}/></th>
            <th><input className='btn' type="button" value="5" onClick={() => pick(5)}/></th>
            <th><input className='btn' type="button" value="6" onClick={() => pick(6)}/></th>
            <th><input className='btn' type="button" value="*" onClick={() => pick('*')} style={{backgroundColor:"green"}}/></th>
        </tr>
        <tr><th><input className='btn' type="button" value="7" onClick={() => pick(7)}/></th>
            <th><input className='btn' type="button" value="8" onClick={() => pick(8)}/></th>
            <th><input className='btn' type="button" value="9" onClick={() => pick(9)}/></th>
            <th><input className='btn' type="button" value="-" onClick={() => pick('-')}  style={{backgroundColor:"green"}}/></th>
        </tr>
        <tr><th><input className='btn' type="button" value="." onClick={() => pick('.')}/></th>
            <th><input className='btn' type="button" value="0" onClick={() => pick('0')}/></th>
            <th><input className='btn' type="button" value="DEL"onClick={() => remove()}  style={{backgroundColor:"blue"}}/></th>
            <th><input className='btn' type="button" value="+" onClick={() => pick('+')}  style={{backgroundColor:"green"}}/></th>
        </tr>
        <tr>
        <th colspan="2"><input className='btn' type="button" value="AC" onClick={() => clr()} style={{backgroundColor:"red"}}/></th>
            <th colspan="2"><input className='btn' type="button" value="=" onClick={() => arthmatic_opration()}  style={{backgroundColor:"green"}}/></th>

        {/* <!-- <th><input type="button" value="0" onclick="pick(0)"></th> --> */}
        {/* <!-- <th><input type="button" value="%" onclick="pick('%')" style="background-color: green;"></th> --> */}

    </tr>

    </Table>
        </Col>
    </Row>
   </Container>
  )
}

export default Calculator
